.title {
  font-family: 'PT Sans Bold', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
  color: #000000;

  &--left {
    text-align: left;
  }

  &--with-image {
    color: #ffffff;
  }

  &--bigger {
    font-size: 21px;
  }
}
